<template>
  <div class="box">
    <h3>J3416自习室智慧门禁系统</h3>
    <div class="form">
    <el-form ref="form" :rules="rules" :model="form" label-width="70px">
      <el-form-item label="用户名" prop="account">
        <el-input v-model="form.account"></el-input>
      </el-form-item>
      <el-form-item label="授权码">
        <el-input v-model="form.password"></el-input>
      </el-form-item>
    </el-form>
    </div>
    <div class="text">
      <span style="color: red">* </span> 用户名如果已经绑定授权码，则再次登录时无需再次输入授权码。仅输入用户名即可。
    </div>
    <el-button type="primary" class="btn" @click="onSubmit">立即授权</el-button>
    <div class="link">
      <el-link type="primary"><i class="el-icon-question"></i> 如何获得授权码？</el-link>
    </div>
  </div>
</template>

<script>
import FingerprintJS from '@fingerprintjs/fingerprintjs'
export default {
  name: 'HelloWorld',
  data() {
    return {
      visitorId:"",
      form: {
        account: '',
        password: '',
      },
      rules: {
        account: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ]
      }
    }
  },
  methods: {
    onSubmit() {
      var account = this.form.account;
      // var password = this.form.password;
      if (account == null || account === ""){
        this.warning("请填写用户名！")
      }else {
        console.log(this.visitorId)
          this.$router.push({
              path:"/home"
          })
      }
    },
    warning(value) {
      this.$message.error(value);
    },
    getUniqueCode(){
      FingerprintJS.load().then(fp => {
        fp.get().then(result => {
          this.visitorId = result.visitorId;
        });
      });
    },
  },
  mounted() {
    this.getUniqueCode()
  }
}
</script>

