<template>
  <div class="box">
    <el-row >
      <el-col>
        <span id="username">{{username}} &nbsp;</span>
        <el-tag v-if="authorization" type="success">永久授权</el-tag>
        <el-tag v-if="!authorization" type="warning">临时授权</el-tag>
      </el-col>
    </el-row>
    <el-row >
      <el-col :sm="12" :lg="6" v-if="isSuccess === 1">
        <el-result icon="success" title="开门成功" subTitle="请离开后随手关门">
          <template slot="extra">
            <el-button type="danger" size="medium" @click="closeRoom">关门</el-button>
          </template>
        </el-result>
      </el-col>
      <el-col :sm="12" :lg="6" v-if="isSuccess === 2" >
        <el-result icon="error" title="操作失败" subTitle="请稍后重新尝试">
          <template slot="extra">
            <el-button type="primary" size="medium" @click="resetRoom">重试</el-button>
          </template>
        </el-result>
      </el-col>
      <el-col :sm="12" :lg="6" v-if="isSuccess === 3">
        <el-result icon="warning" title="操作选项" subTitle="请选择要进行的操作">
          <template slot="extra">
            <el-button type="primary" size="medium" @click="openRoom" >开门</el-button>
            <el-button type="danger" size="medium" @click="closeRoom">关门</el-button>
          </template>
        </el-result>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data(){
    return{
      isSuccess: 3,
      des:true,
      username:"测试姓名",
      authorization:true
    }
  },
  methods:{
    openRoom(){
      this.isSuccess=1
      this.des= false
    },
    closeRoom(){
      this.isSuccess=3;
      this.des= true
      if (this.isSuccess === 3){
        this.$message.success("操作成功！");
      }
    },
    resetRoom(){
      if (this.des){
        this.closeRoom();
      }else {
        this.openRoom();
      }
    }
  }
}
</script>

